<template>
  <div>
    <mt-header title="门店列表" :style="{background:all.window.navigationBarBackgroundColor}">
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div>
      <div class="select_meal">
        <p>选择门店</p>
      </div>
      <div class="select_meal_list" @click="selectRoom(-1)">
        <p>不限门店</p>
        <p> > </p>
      </div>
      <div class="select_meal_list" v-for="(item,key) in getSortsList" @click="selectRoom(key)">
          <div>
            <p style="margin-bottom: 10px">{{item.stores_name}}</p>
            <p style="color:#9f9f9f;">地址: <span>{{item.stores_address}}</span></p>
          </div>
          <p> > </p>
      </div>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          sheetVisible:false,
        }
    },
  computed:{
    getSortsList(){
      return this.$store.getters.getSortsList;
    },
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    share(){
      this.sheetVisible = true;
    },
    selectRoom(val){
      this.$store.dispatch("mealState",val);
      this.$router.push("/index");
    },
  }
}
</script>
<style scoped>
.select_meal{
  padding: 10px;
  background-color: #eee;
}
.select_meal_list{
  padding: 10px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>



