<template>
    <div class="book">
        <header class="bg_img" :style="{backgroundImage:`url(${all.window.navigationBarBg})`}">
          <div class="title">
            <h1>{{all.window.navigationBarTitleText}}</h1>
            <p>{{all.window.navigationBarTitle}}</p>
          </div>
        </header>
        <section class="book_message">
          <p class="list"><strong>立即预定</strong></p>
          <p class="list">地址</p>
          <p class="list space" v-if="getSortsList.length === 0">
            <span>
                  河南郑州世玺中心
            </span>
            <span style="color: red">查看地图</span>
          </p>
          <p class="list space" v-else @click="selectMeal">
            <span v-if="state === -1">
              不限门店
            </span>
            <span style="color: red" v-if="state === -1">共{{getSortsList.length}}家门店</span>
            <span v-if="state !== -1">
              <div>{{getSortsList[state].stores_name}}</div>
              <div style="margin-top: 5px;color:#9f9f9f;">地址:{{getSortsList[state].stores_address}}</div>
            </span>
            <span style="color: red" v-if="state !== -1">查看地图</span>
          </p>
          <p class="list">时间</p>
          <p class="list space">
            <span @click="startTime">{{start_time}}</span>
            <mt-datetime-picker
              ref="picker"
              v-model="pickerVisible"
              type="date"
              year-format="{value} 年"
              month-format="{value} 月"
              date-format="{value} 日">
            </mt-datetime-picker>
            <span class="total">{{(new Date(end_time).getTime() - new Date(start_time).getTime())/(1000*60*60*24)}}晚</span>
            <span @click="endTime">{{end_time}}</span>
            <mt-datetime-picker
              ref="endPicker"
              v-model="endPickerVisible"
              type="date"
              year-format="{value} 年"
              month-format="{value} 月"
              date-format="{value} 日">
            </mt-datetime-picker>
          </p>
          <p class="list"><mt-button size="large" @click="search"  style="color: white" :style="{backgroundColor:all.window.navigationBarBackgroundColor}"><span>搜索房源</span></mt-button></p>
          <p class="list tel">电话: 230245789</p>
        </section>
    </div>
</template>
<script>

export default{
    data(){
        let that = this;
        return {
          msg:"不限门店",
          selected:"1",
          pickerVisible:"",
          endPickerVisible:"",
          start_time:`${new Date().getFullYear()}-${new Date().getMonth()+1}-${new Date().getDate()}`,
          num:'1',
          end_time: ''
        }
    },
    computed:{
      state(){
        return this.$store.getters.getMealState;
      },
      getGoodsList(){
          return this.$store.getters.getGoodsList;
      },
      getSortsList(){
        return this.$store.getters.getSortsList;
      },
      all(){
        return this.$store.getters.getAll;
      }
    },
    watch:{
      start_time:{
        deep:true,
        handler(val){
            return val;
        }
      },
      end_time(val){
          return val;
      }
    },
    methods:{
      selectMeal(){
        this.$router.push("/mealList")
      },
      startTime(){
        this.$refs.picker.open();
        let time = new Date(this.pickerVisible).getTime();
        this.start_time = new Date(time).getFullYear() + "-" + (new Date(time).getMonth()-0+1) +'-'+ new Date(time).getDate();
        this.$store.dispatch("startTime",this.start_time);
      },
      endTime(){
        this.$refs.endPicker.open();
        let time = new Date(this.endPickerVisible).getTime();
        this.end_time = new Date(time).getFullYear() + "-" + (new Date(time).getMonth()-0+1) +'-'+ new Date(time).getDate();
        this.$store.dispatch("endTime",this.end_time);
      },
      search(){
        if (this.state === -1){
//            不限门店
          this.$router.push('/pay')
        }else {
          let url = '/mealSelect/'+this.state;
          this.$router.push(url)
        }
      }
    },
  created(){
        let time = new Date(this.start_time).getTime() + 24*60*60*1000;
        this.end_time = new Date(time).getFullYear() + '-' +  (new Date(time).getMonth()-0+1) +'-'+ new Date(time).getDate();
        this.$store.dispatch("startTime",this.start_time);
        this.$store.dispatch("endTime",this.end_time);
  }
}
</script>
<style scoped>
  .book{  position: relative;  }
  .bg_img{  height: 40vh;  background:url("/static/image/banner1.png") no-repeat;  background-size: contain;  }
  .title{
    width: 100vw;
    height: 40vh;
    background-color: rgba(0, 0, 0, 0.3);
    /*opacity: 0.5;*/
    position: absolute;
    top:0;
    z-index: 5;
  }
  .title>h1{
    color: white;
    width: 40vw;
    margin-left: 10vw;
    margin-top: 10vh;
    font-size: 22px;
  }
  .title>p{
    color: white;
    width: 80vw;
    margin-left: 10vw;
  }
  .book_message{
    width: 90vw;
    height: 52vh;
    position: absolute;
    background-color: white;
    top:30vh;
    left: 5vw;
    border-radius: 10px;
    z-index: 10;
    padding: 10px 20px;
}
  .list{
    padding-top: 15px;
  }
  .space{
    /*padding-bottom: 5px;*/
    padding-bottom: 2px;
    border-bottom:1px solid #eee ;
    display: flex;
    justify-content: space-between;
  }
  .total{
    display: flex;
    background-color: red;
    color: white;
    border-radius: 50%;
    font-size: 12px;
  }
  .tel{
    text-align: center;
  }
</style>



