import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import payInfo from '@/components/payInfo'
import index from '@/components/index'
import roomInfo from '@/components/goods/roomInfo'
import order from '@/components/order'
import more from '@/components/more/more'
import about from '@/components/more/about'
import pay from '@/components/pay'
import list from '@/components/sorts/list'
import people from '@/components/people/people'
import mealList from "@/components/mealList"
import mealSelect from "@/components/mealSelect"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      // redirect:"/index",
      component:home,
        children:[,
        {
          path:'/order',
          name:"order",
          component:order
        },{
          path:'/more',
          name:"more",
          component:more
        },
        {
          path:'/index',
          name:"index",
          component:index
        }
        ]
    },
    {
      path:'/mealSelect/:id',
      name:"mealSelect",
      component:mealSelect
    },   {
      path:'/mealList',
      name:"mealList",
      component:mealList
    },  {
      path:'/payInfo/:mealId/:roomId',
      name:"payInfo",
      component:payInfo
    },  {
      path:'/roomInfo/:mealId/:roomId',
      name:"roomInfo",
      component:roomInfo
    },{
      path:'/about',
      name:"about",
      component:about
    },{
      path:'/pay',
      name:"pay",
      component:pay
    },{
      path:'/list',
      name:"list",
      component:list
    },{
      path:'/people',
      name:"people",
      component:people
    }
  ]
})



