<template>
  <div class="meals">
    <mt-header :title="all.window.navigationBarTitleText" :style="{background:all.window.navigationBarBackgroundColor}">
      <!--<mt-header title="西瓜住宿">-->
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <header class="condition" :style="{background:all.window.navigationBarBackgroundColor}">
      <span>已选条件</span>
      <span>{{start_time}}至{{end_time}}</span>
      <span @click="clearSelect">清除选择</span>
    </header>
    <div class="meal_type" :style="{background:all.window.navigationBarBackgroundColor}">
      <p>{{mealList.stores_name}}</p>
      <p>{{mealList.rooms | mealSum}}间空房</p>
    </div>
    <div class="roomList">
      <div class="item"  @click="buyRoom(id,key)" v-for="(item,key) in mealList.rooms">
        <img :src="item.photo | webPhoto" alt="">
        <div class="room_info">
          <section>
            <p style="margin-bottom: 10px"><strong>{{item.room_type}}</strong></p>
            <p>{{item.bed_type}}</p>
          </section>
          <p style="color: red">均价¥{{item.price}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
      data(){
          return{
            sheetVisible:false,
            id:0,
          }
      },
    computed:{
      mealList(){
        let obj = {};
        obj = this.$store.getters.getSortsList[this.id];
        return obj;
      },
      all(){
        return this.$store.getters.getAll;
      },
      getSortsList(){
        return this.$store.getters.getSortsList;
      },
      start_time(){
        return this.$store.getters.getStartTime;
      },
      end_time(){
        return this.$store.getters.getEndTime;
      },
    },
    methods:{
      buyRoom(id,key){
          let url = `/roomInfo/${id}/${key}`;
//          console.log(id,key);
        this.$router.push(url)
      },
      share(){
         this.sheetVisible = true;
      },
      clearSelect(){
        this.$router.push("/index")
      },
    },
    mounted(){
        this.id = this.$route.params.id;
    },
    filters:{
      mealSum(val){
        let number = [];
        val.forEach(item => {
          number.push(item.number);
        });
        return number.reduce((pre,next) => {
          return pre + next;
        })
      },
      webPhoto(val){
        return val ? "http://weapp.xiguawenhua.com/" + val.split(',')[0] : "../assets/oh.png"
      }
    },
  }
</script>
<style scoped>
  .meals{
    height: 100vh;
    overflow-y: auto;
  }
  .meals::-webkit-scrollbar{
    width:0.05rem;
    height:0.1rem;
  }
  .meals::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: rgba(0, 0, 0, 0.2);
  } /* 滑块颜色 */
  .condition{
    padding:0 20px;
    height: 10vw;
    background-color: black;
    display: flex;
    justify-content:space-between;
    align-items: center;
  }
  .condition>span{
    color: white;
  }
  .meal_type{
    padding: 15px;
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-between;
  }
  .item>img{
    height: 20vh;
    width: 100%;
  }
  .item{
    margin-top: 10px;
    width: 90vw;
    height: 30vh;
    margin-left: 5vw;
    background-color: #eee;
    border-radius: 10px;
  }
  .room_info{
    padding:10px 20px;
    display: flex;
    justify-content: space-between;
  }
</style>



