<template>
  <div class="roomInfo">
    <mt-header title="订单" :style="{background:all.window.navigationBarBackgroundColor}">
    <!--<mt-header title="西瓜住宿">-->
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="room_content">
        <div class="room_img">
          <img :src="room.photo | photo" alt="">
        </div>
        <div class="room_info">
          <p><strong>{{room.room_type}}</strong></p>
          <p style="color: red;margin-top: 10px"><strong>均价¥ {{room.price}} /晚</strong></p>
        </div>
        <div class="room_info">
          <p>服务热线:{{roomTel.stores_tell}}</p>
        </div>
        <div class="room_info">
          <p>酒店位置:{{roomTel.stores_address}}</p>
        </div>
        <div class="wxpay">
          <span>微信支付</span>
        </div>
        <div class="room_info">
          <p><strong>基本信息</strong></p>
        </div>
        <div class="room_info base">
          <p>容纳人数: <span class="info">最多容纳{{room.number_in}}人</span></p>
          <p>房间面积: <span class="info">{{room.area}}平米</span></p>
          <p>入住时间: <span class="info">14:00以后</span></p>
          <p>退房时间: <span class="info">12:00</span></p>
          <p>房铺类型: <span class="info">{{room.bed_type}}</span></p>
        </div>
        <div class="room_info">
          <p><strong>便利措施</strong></p>
        </div>
        <div class="room_info base">
          <p v-for="roomInfo in room.facilities"><img :src="roomInfo.imgUrl | img" alt="" style="width: 20px;max-height:100%;">
            <span style="margin-left: 10px">{{roomInfo.name}}</span>
          </p>
        </div>
    </div>
    <footer>
        <p>客服</p>
        <p class="btn"><mt-button type="danger" size="large" @click="book" :style="{background:all.window.navigationBarBackgroundColor}"><span>立即预定</span></mt-button></p>
    </footer>
    <mt-popup
      v-model="popupVisible"
      position="bottom"
      popup-transition="popup-fade">
        <p class="pop_item">
          <span>价格</span>
          <span style="color: red">¥{{room.price}} 1晚</span>
        </p>
      <p class="pop_item">
        <span>日期</span>
        <span>{{start_time}}至{{end_time}}</span>
      </p>
      <p class="pop_item">
        <mt-button type="danger" size="large" @click="pay(mealId,roomId)" :style="{background:all.window.navigationBarBackgroundColor}"><span>立即预定</span></mt-button>
      </p>
    </mt-popup>
  </div>
</template>
<script>
export default{
    data(){
        return{
          popupVisible:false,
          sheetVisible:false,
          mealId:0,
          roomId:0,
          flag:false,
        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    },
    room(){
      let obj = {};
      for (let item in this.$store.getters.getSortsList[this.mealId].rooms[this.roomId]) {
        obj[item] = this.$store.getters.getSortsList[this.mealId].rooms[this.roomId][item];
      }
      obj['facilities'] = JSON.parse(obj['facilities']);
      return obj;
    },
    roomTel(){
      return this.$store.getters.getSortsList[this.mealId]
    },
    start_time(){
        return this.$store.getters.getStartTime
    },
    end_time(){
      return this.$store.getters.getEndTime
    }
  },
  methods:{
    pay(meal,room){
      let url = `/payInfo/${meal}/${room}`;
      this.$router.push(url)
    },
    book(){
      this.popupVisible = true;
    },
    share(){
      this.sheetVisible = true;
    }
  },
  mounted(){
    this.mealId = this.$route.params.mealId;
    this.roomId = this.$route.params.roomId;
  },
  filters:{
    facilities(val){
      console.log(val);
    },
    photo(val){
          return val ? "http://weapp.xiguawenhua.com/" + val.split(",")[0] : "/assets/oh.png"
      },
    img(val){
          return val ? "./static/image/"+val : '';
    }
  }
}
</script>
<style scoped>
.room_img>img{
  width:100%;
  max-height: 100%;
}
  .room_info{
   padding:10px 20px;
    border-bottom: 1px solid #eee;
  }
  .base{
    display: flex;
    flex-wrap: wrap;
  }
  .base>p{
    flex: 0 1 45%;
  }
  .room_info>p{
    margin-left: 10px;
    margin-top: 10px;
  }

  .wxpay{
    padding:10px 20px;
  }
footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
  .btn{
    flex-basis: 90%;
  }
  .room_content{
    height: calc(100vh - 90px);
    overflow: auto;
  }
.room_content::-webkit-scrollbar{
  width:0.05rem;
  height:0.1rem;
}
.room_content::-webkit-scrollbar-thumb {
  border-radius: 0.1rem;
  background-color: rgba(0, 0, 0, 0.2);
} /* 滑块颜色 */
.pop_item{
  padding: 10px 20px;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  border: 1px solid #eee;
}
</style>



