<template>
  <div class="pay">
    <mt-header :title="all.window.navigationBarTitleText" :style="{background:all.window.navigationBarBackgroundColor}">
    <!--<mt-header title="西瓜住宿">-->
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <header class="condition" :style="{background:all.window.navigationBarBackgroundColor}">
      <span>已选条件</span>
      <span>{{start_time}}至{{end_time}}</span>
      <span @click="clearSelect">清除选择</span>
    </header>
    <div class="meal_type" v-if="getSortsList.length>1" v-for="(item,key) in getSortsList" @click="selectRoom(key)">
      <div>
        <p>{{item.stores_name}}</p>
        <p>地址:{{item.stores_address}}</p>
      </div>
      <div>
        <p style="color: red">¥ {{item.rooms | minPrice}} 起</p>
        <p>{{ item.rooms | sumAdd}}间空房</p>
      </div>
    </div>
    <!--<div class="roomList" v-else>-->
        <!--<div class="item" @click="buyRoom">-->
          <!--<img src="../assets/oh.png" alt="">-->
          <!--<div class="room_info">-->
            <!--<section>-->
              <!--<p style="margin-bottom: 10px"><strong>单人间</strong></p>-->
              <!--<p>2米双人床</p>-->
            <!--</section>-->
            <!--<p style="color: red">均价¥100</p>-->
          <!--</div>-->
        <!--</div>-->
    <!--</div>-->
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return {
        color:'blue',
        sheetVisible:false,
        number:0
      }
    },
    computed:{
      getSortsList(){
        return this.$store.getters.getSortsList;
      },
      start_time(){
        return this.$store.getters.getStartTime;
      },
      end_time(){
        return this.$store.getters.getEndTime;
      },
      all(){
        return this.$store.getters.getAll;
      },
      people(){
        return this.$store.getters.getPeople;
      },
      bookInfo(){
          return this.$store.getters.getBookInfo;
      }
    },
    methods: {
      selectRoom(key){
        let url = '/mealSelect/'+key;
        this.$router.push(url)
      },
      buyRoom(){
        this.$router.push('/roomInfo')
      },
      clearSelect(){
        this.$router.push("/index")
      },
      submitOrder(){
        MessageBox('提示', '预览模式暂不支持,请手机预览');
      },
      share(){
        this.sheetVisible = true;
      },
    },
    filters:{
      sumAdd(val){
            let number = [];
            val.forEach(item => {
              number.push(item.number);
            });
         return number.reduce((pre,next) => {
              return pre + next;
          })
      },
      minPrice(val){
          let price = [];
          val.forEach(item => {
             price.push(item.price-0);
          });
          return Math.min.apply(Math,price)
      }
    }
  }
</script>
<style scoped>
.condition{
  padding:0 20px;
  height: 10vw;
  background-color: black;
  display: flex;
  justify-content:space-between;
  align-items: center;
}
.condition>span{
  color: white;
}
.item{
    margin-top: 10px;
    width: 90vw;
    height: 30vh;
    margin-left: 5vw;
    background-color: #eee;
    border-radius: 10px;
  }
.item>img{
  height: 20vh;
  width: 100%;
}
.room_info{
  padding:10px 20px;
  display: flex;
  justify-content: space-between;
}
.meal_type{
  padding: 10px;
  border-bottom: #eee;
  display: flex;
  justify-content: space-between;
  background-color: white;
}
  .meal_type>div>p:first-child{
    margin-bottom: 10px;
  }
</style>



