<template>
  <div class="roomInfo">
    <mt-header title="订单" :style="{background:all.window.navigationBarBackgroundColor}">
    <!--<mt-header title="预定">-->
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="room_info">
      <div class="room_img">
        <img :src="room.photo | photo" alt="">
        <span>{{room.room_type}}</span>
      </div>
      <div class="room_info space">
        <span>日期: {{start_time}} 至 {{end_time}}</span>
        <span style="color: red">¥ {{room.price}}</span>
      </div>
    </div>
    <div class="pay">
      <span>支付方式</span>
    </div>
    <div class="room_info space">
      <span>微信支付</span>
      <span style="color: green;font-size: 20px">√</span>
    </div>
    <div class="pay">
      <span>入住信息</span>
    </div>
    <div class="room_info">
      <p style="border-bottom: 1px solid #eee">
        <mt-field label="入住人"></mt-field>
      </p>
        <mt-field label="手机号"></mt-field>
    </div>
    <div class="pay">
      <span>留言</span>
    </div>
    <div class="room_info">
      <mt-field placeholder="有其他需求请填写" type="textarea" rows="3"></mt-field>
    </div>
    <footer>
      <mt-button type="danger" size="large" @click="pay" :style="{background:all.window.navigationBarBackgroundColor}"><span>立即预定</span></mt-button>
    </footer>
    <mt-popup
      v-model="popupVisible"
      popup-transition="popup-fade"
      position="top">
      <p class="not_see">
        预览暂不支持下单
      </p>
    </mt-popup>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        popupVisible:false,
        sheetVisible:false,
        mealId:0,
        roomId:0
      }
    },
    computed:{
      all(){
        return this.$store.getters.getAll;
      },
      room(){
        let obj = {};
        obj = this.$store.getters.getSortsList[this.mealId].rooms[this.roomId];
        return obj;
      },
      start_time(){
        return this.$store.getters.getStartTime
      },
      end_time(){
        return this.$store.getters.getEndTime
      }
    },
    methods:{
      share(){
        this.sheetVisible = true;
      },
      pay(){
          this.popupVisible = true;
      }
    },
    mounted(){
      this.mealId = this.$route.params.mealId;
      this.roomId = this.$route.params.roomId;
    },
    filters:{
      photo(val){
        return val ? "http://weapp.xiguawenhua.com/" + val.split(",")[0] : "/assets/oh.png"
      }
    }
  }
</script>
<style scoped>
.room_info{
  padding: 10px 15px;
}
.room_img>img{
    width: 130px;
    height: 80px;
}
.room_img>span{
    margin-left: 10px;
}
.space{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
  .pay{
    padding:10px 20px;
    background-color: #eee;
  }
footer{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
.not_see{
    padding:10px;
    width: 100vw;
    text-align: center;
    background-color: red;
    color: white;
  }
</style>



